import { Col, Row, FormControl, InputGroup, Button } from 'react-bootstrap';
import { Facebook, Linkedin, Twitter, Whatsapp, Youtube } from 'react-bootstrap-icons';
import aiesecLogo from './Images/aiesecLogo.svg';
import Link from 'react-dom';

const Footer = () =>{
    return (
        <>
        <img center src={aiesecLogo} alt='logobig' />

            <Row>
                <Col>
                <h4>About</h4>
                <p>About us</p>
                <p>Annual Report</p>

                <h4>Organizations</h4>
                <p>Partner with us</p>
                <p>Pricing</p>
                </Col>
                
                <Col>
                <h4>Youth</h4>
                <p>Youth 4 Global Goals</p>
                <p>Youth Speak</p>
                
                <Link to="https://github.com/Joeyryanbridges">
  <img src="facebook.svg" className="githubIcon" />
</Link>
                <Facebook />
                <Linkedin />
                <Youtube />
                <Whatsapp />
                <Twitter />
                </Col>
                
                <Col>
                <h4>NEWS LATTER</h4>
                <p>stay update with last offers and news</p>
                
                <InputGroup className="mb-3">
    <FormControl
      placeholder="Search for Places & Milestones "
      aria-label="Recipient's username"
      aria-describedby="basic-addon2"
    />
    <Button variant="primary" size="lg" id="button-addon2">
    Send 
    </Button>
  </InputGroup>

                <h4>Membership</h4>
                <p>Join Us</p>
                <p>Find your country!</p>
                </Col>
            </Row>
        </>   
    );
};
export default Footer;